import React, { Component } from 'react';
import { Avatar, Button, Card, CardContent, Grid, Stack, Divider } from '@mui/material';
import AddCircleIcon from '@mui/icons-material/AddCircle';

class Country extends Component {
    state = {
        name: 'United States',
        medals: 0
    }
    addMedal = () => {
        this.setState({ medals: this.state.medals + 1});
    }
    render() {
        return (
            <Grid container justifyContent="center" sx={{mt: 2}}>
                <Card sx={{minWidth: 300}}>
                    <CardContent>
                        <h3 className='name'>{this.state.name}</h3>
                        <Divider varient='middle' sx={{my: 2}}/>
                        <Grid container>
                            <Grid item justifyContent='start' xs={8}>
                                <Stack direction='row' spacing={1}>
                                    <Avatar sx={{backgroundColor: 'gold', color: 'black'}}>{this.state.medals}</Avatar>
                                    <span id='medals-label'>Gold Medals</span>
                                </Stack>
                            </Grid>
                            <Grid item justifyContent='start' xs={4}>
                                <Button variant='contained' onClick={this.addMedal}><AddCircleIcon></AddCircleIcon></Button>
                            </Grid>
                        </Grid>
                    </CardContent>
                </Card>
            </Grid>
        )
    }
}

export default Country;